.page-footer {
  padding: 30px 0;

  background-color: $dark-grey;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  & .header__phone {
    margin-top: 0;
  }
}

.page-footer__copyright {
  margin-bottom: 0;

  font-size: 12px;
  color: $basic-white;
}

@media screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    align-items: flex-start;

    & .header__phone {
      margin-top: 20px;
    }
  }
}
