.price {
  margin: 70px 0;

  background-color: $basic-white;
}

.price__title {
  font-weight: 800;
  font-size: 20px;
  text-align: center;
}

.price__subtitle {
  font-size: 14px;
  text-align: center;
  color: $special-black;
}

.price__list {
  @include reset-list;

  width: 70%;
  margin: 0 auto;
  margin-top: 40px;
  padding: 40px;

  background-color: $basic-grey;
  border-radius: 5px;
}

.price__list-item {
  margin-bottom: 10px;
  padding-left: 20px;

  font-size: 14px;
  color: $special-black;

  background-image: url("../img/dot.svg");
  background-repeat: no-repeat;
  background-position: left 6px;

  &:last-child {
    margin-top: 40px;
    margin-bottom: 0;

    background-image: none;
  }

  & span {
    font-weight: 700;
  }
}

@media screen and (max-width: 991px) {
  .price__list {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .price {
    margin: 50px 0;
  }
}

@media screen and (max-width: 500px) {
  .price__title {
    font-size: 18px;
  }

  .price__list {
    padding: 30px;
  }
}


@media screen and (max-width: 400px) {
  .price__list {
    padding: 20px;
  }
}
