.main-screen {
  padding: 80px 0;

  background-color: $basic-light-grey;
}

.main-screen--wrapper {
  display: flex;
  align-items: center;
}

.main-screen__list-item-link {
  color: $special-black;
  font-weight: 600;

  &:hover {
    color: $special-black;
    opacity: 0.8;
  }
}

.main-screen__title {
  font-weight: 800;
  font-size: 28px;

  & span {
    color: $basic-yellow;
  }
}

.main-screen__list {
  @include reset-list;

  margin-top: 30px;
}

.main-screen__list-item {
  margin-bottom: 10px;
  padding-left: 20px;

  font-size: 14px;
  color: $special-black;

  background-image: url("../img/dot.svg");
  background-repeat: no-repeat;
  background-position: left 5px;

  & span {
    font-weight: 600;
  }
}

.main-screen__img {
  width: 100%;
}

@media screen and (max-width: 991px) {
  .main-screen__img {
    display: block;
    width: 60%;
    margin: 0 auto;
    margin-top: 30px;
  }
}

@media screen and (max-width: 768px) {
  .main-screen {
    padding: 60px 0;
  }

  .main-screen__img {
    display: block;
    width: 80%;
    margin: 0 auto;
    margin-top: 30px;
  }
}

@media screen and (max-width: 567px) {
  .main-screen__img {
    display: block;
    width: 80%;
    margin: 0 auto;
    margin-top: 30px;
  }
}

@media screen and (max-width: 500px) {
  .main-screen__title {
    font-size: 24px;
  }

  .main-screen__img {
    width: 100%;
  }
}
