.process {
  padding: 70px;
  margin-top: 70px;

  background-color: $basic-grey;
}

.process__title {
  font-weight: 800;
  font-size: 20px;
  text-align: center;
}

.process__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  margin-top: 40px;
}

.process__list-item {
  margin-bottom: 30px;
  padding: 30px;
  padding-left: 80px;

  font-size: 14px;
  color: $special-black;

  background-color: $basic-white;
  background-repeat: no-repeat;
  background-position: 10px 30px;
  border-radius: 5px;
}

.process__list-item-header {
  margin: 10px 0;
  font-size: 16px;
  font-weight: 600;
}

.process__list-item--ticket {
  width: 48%;

  background-image: url("../img/ticket.svg");
}

.process__list-item--plane {
  width: 48%;

  background-image: url("../img/plane.svg");
}

.process__list-item--car {
  width: 48%;

  background-image: url("../img/car.svg");
}

.process__list-item--money {
  width: 48%;

  background-image: url("../img/money.svg");
}

@media screen and (max-width: 1400px) {
  .process__list {
    width: 80%;
  }

  .process__list-item--ticket {
    width: 100%;
  }

  .process__list-item--plane {
    width: 100%;
  }

  .process__list-item--car {
    width: 48%;
  }

  .process__list-item--money {
    width: 48%;
  }
}

@media screen and (max-width: 991px) {
  .process__list {
    width: 100%;
  }

  .process__list-item--ticket {
    width: 100%;
  }

  .process__list-item--plane {
    width: 100%;
  }

  .process__list-item--car {
    width: 100%;
  }

  .process__list-item--money {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .process {
    margin-top: 50px;
    padding: 50px 0;
  }
}

@media screen and (max-width: 500px) {
  .process__title {
    font-size: 18px;
  }

  .process__list-item {
    padding: 20px;
    padding-left: 70px;

    background-position: 5px 20px;
  }
}
