.advantages {
  padding: 70px 0;

  background-color: $basic-white;
}

.advantages__title {
  font-weight: 800;
  font-size: 20px;
  text-align: center;
}

.advantages__list {
  @include reset-list;

  display: grid;
  width: 100%;
  margin: 0 auto;
  margin-top: 40px;

  grid-gap: 30px;
  grid-template-columns: 1fr 1fr;
}

.advantages__list-item {
  padding: 30px;
  padding-left: 80px;

  font-size: 14px;
  color: $special-black;

  background-color: $basic-light-grey;
  background-image: url("../img/star.svg");
  background-repeat: no-repeat;
  background-position: 20px 30px;
  border-radius: 5px;
}

@media screen and (max-width: 1400px) {
  .advantages__list {
    width: 80%;

    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 991px) {
  .advantages__list {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .advantages {
    padding: 50px 0;
  }

  .advantages__title {
    & br {
      display: none;
    }
  }
}

@media screen and (max-width: 500px) {
  .advantages__title {
    font-size: 18px;
  }

  .advantages__list-item {
    padding: 20px;
    padding-left: 60px;

    background-position: 10px 20px;
  }
}
