.page-header {
  padding: 20px 0;

  background-color: $dark-grey;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__phone--wrapper {
  display: flex;
  align-items: center;
}

.header__name {
  color: $basic-white;
  font-size: 15px;
  margin-right: 10px;
  margin-bottom: 0;

  padding-bottom: 30px;
  padding: 0 0 20px 40px;
  background-image:  url("../img/viber.svg"), url("../img/tele.svg"), url("../img/watsapp.svg");
  background-position: left bottom, center bottom, right bottom;
  background-repeat: no-repeat;
}

.header__text {
  margin-bottom: 0;

  font-size: 12px;
  color: $basic-white;
}

.header__phone {
  text-align: right;
  width: 170px;
  margin-bottom: 0;
  padding-left: 20px;
  margin-bottom: 5px;

  font-weight: 500;
  font-size: 13px;
  color: $basic-white;
  text-decoration: none;

  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
  background-position: 45px center;

  &:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 768px) {
  .header {
    align-items: flex-start;
  }
}

@media screen and (max-width: 550px) {
  .header {
    flex-direction: column;
  }

  .header__phone {
    margin-top: 20px;
  }
}
